/*
  Stylguidist reset styles
*/
/**
 * Colors
 */
html {
  font-size: 62.5%;
  height: 100%; }

[class*="rsg--body-"] [class*="rsg--header-"] {
  margin-bottom: 0; }
  [class*="rsg--body-"] [class*="rsg--header-"] [class*="rsg--heading-"] {
    font-size: 3.2rem;
    font-weight: 700; }

[class*="rsg--body-"] [class*="rsg--root-"] {
  margin-bottom: 20px; }

[class*="rsg--body-"] [class*="rsg--docs-"] {
  margin-bottom: 20px;
  border-bottom: 4px solid #f5f5f5; }
  [class*="rsg--body-"] [class*="rsg--docs-"] .rsg--para-39 {
    margin-bottom: 10px; }

[class*="rsg--body-"] [class*="rsg--heading4-"] {
  padding-top: 20px;
  border-top: 4px solid #f5f5f5;
  font-weight: 500; }

[class*="rsg--body-"] [class*="rsg--preview-"] {
  border: none;
  padding: 20px 20px 40px 0; }
  [class*="rsg--body-"] [class*="rsg--preview-"] > div {
    display: flex;
    align-items: flex-end; }

[class*="rsg--body-"] [class*="rsg--button-"] {
  text-transform: none;
  font-size: 14px;
  padding: 0 0 5px 0;
  margin-bottom: 15px; }
  [class*="rsg--body-"] [class*="rsg--button-"] [class*="rsg--isActive-"] {
    border-bottom: 1px #e90 solid;
    color: #e90; }

[class*="rsg--body-"] [class*="rsg--table-"],
[class*="rsg--body-"] [class*="rsg--tabs-"] {
  margin-bottom: 0; }

[class*="rsg--body-"] [class*="rsg--sidebar-"] [class*="rsg--link-"],
[class*="rsg--body-"] [class*="rsg--sidebar-"] [class*="rsg--link-"]:link,
[class*="rsg--body-"] [class*="rsg--sidebar-"] [class*="rsg--link-"]:visited {
  font-weight: 500; }

[class*="rsg--body-"] [class*="rsg--spacing-"] {
  margin: 0; }

[class*="rsg--body-"] [class*="rsg--para-"] {
  font-size: 1.4rem; }

[class*="rsg--footer-"] [class*="rsg--para-"] {
  display: none; }

/**
 * Helper classes
*/
.bold {
  font-weight: 700; }

